<template>
  <div>
    <!--  -->
    <Breadcrumb :bread="orderbread" />

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder=""><el-button slot="append" icon="el-icon-search"></el-button></el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderlist" border stripe>
        <el-table-column type="index" label="#" width="60px" align="center"></el-table-column>
        <el-table-column label="" prop="order_number" align="center"></el-table-column>
        <el-table-column label="" prop="order_price" width="90px" align="center"></el-table-column>
        <el-table-column label="" prop="pay_status" width="90px" align="center">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="90px" align="center"></el-table-column>
        <el-table-column label="" prop="create_time" width="200px" align="center">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="120px" align="center">
          <template>
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"> </el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"> </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 100, 1000]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressDialogClosed">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px" :props="{ expandTrigger: 'hover' }">
        <el-form-item label="/" prop="address1">
          <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="addressVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import Breadcrumb from '../../components/Breadcrumb.vue'
import cityData from './citydata'
export default {
  components: { Breadcrumb },
  data() {
    return {
      orderbread: ['', ''],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5,
      },
      total: 0,
      orderlist: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        address1: '',
      },
      addressFormRules: {
        address1: [
          {
            required: true,
            message: '/',
            trigger: 'blur',
          },
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      cityData,
      progressVisible: false,
      progressInfo: [],
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo,
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.total = res.data.total
      this.orderlist = res.data.goods
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    //
    showBox() {
      this.addressVisible = true
    },
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },

    async showProgressBox() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      this.progressVisible = true

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.progressInfo = res.data
    },
  },
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
